// Home.js
import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import Contact from './Contact';
import './home.css';
import './font.css'
import NewNavbar from './Navbar';
import Employees from './Employees'; // Import the Employees component
import Service from './Service'; // Import the Employees component
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css'; // 引入Swiper的样式
import { EffectFade, Autoplay, Pagination, Navigation } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Col, Container, Row, Image } from 'react-bootstrap';

const slides = [

  { src: '/images/image2.jpg', alt: 'Slide 2' },
  { src: '/images/image3.jpg', alt: 'Slide 3' },
  { src: '/images/image4.jpg', alt: 'Slide 4' },
  { src: '/images/image5.jpg', alt: 'Slide 5' },

];


const GSheetReader = require('g-sheets-api');

function Home() {


  const [activeSlide, setActiveSlide] = useState(0);
  const [showModal, setShowModal] = useState(true);
  const [homeTitleText, setHomeTitleText] = useState(''); // 状态用于存储从Google Sheets获取的标题
  const [h1, setH1] = useState('');
  const [p1, setP1] = useState('');
  const [side_background, setSB] = useState('');
  const [discount, setDC] = useState('');
  // const HomeH1 = configData.HomeH1
  const [HomeH1, setHomeH1] = useState("Welcome to Asian Beauty Salon!");



  // const imageUrl = '/images/image1.jpg';
  // const backgroundImage = `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('/images/image1.jpg') no-repeat 0% 20%`;
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex(prevIndex => (prevIndex + 1) % slides.length);
    }, 5000); // 每3秒切换一次

    return () => clearInterval(interval); // 清理定时器
  }, []);

  const backgroundImage = `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('${slides[currentIndex].src}') no-repeat 0% 20%`;




  useEffect(() => {
    fetch('/config.json')
      .then(response => response.json())
      .then(data => {
        // console.log(data.HomeH1);
        setHomeH1(data.HomeH1);
      })
      .catch(error => console.error('Error loading config:', error));
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setActiveSlide((prevSlide) => (prevSlide + 1) % slides.length);
    }, 3000);
    return () => clearInterval(interval);
  }, [slides.length]);

  // Function to open the modal popup
  const openModal = () => {
    setShowModal(true);
  };

  // Function to close the modal popup
  const closeModal = () => {
    setShowModal(false);
  };

  // read google sheet data

  function getValueByKey(results, key) {
    const item = results.find(entry => entry.key === key);
    return item ? item.Value : ""; // 如果找到，返回 Value，否则返回 null
  }

  useEffect(() => {
    const options = {
      apiKey: 'AIzaSyAKMN26phfEvXQKd9o-za_hpzHlptGzBFA',
      sheetId: '1cG4Tlm1YgdLCZKuivkSyy8pOcx-YzEZ5Trxh8GYvhDY',
      sheetName: "工作表1",
    };

    GSheetReader(options, results => {
      setHomeTitleText(getValueByKey(results, "HomeTitleText"));
      setH1(getValueByKey(results, "h1"));
      setP1(getValueByKey(results, "p1"));
      setSB(getValueByKey(results, "side_bg"));
      setDC(getValueByKey(results, "discount"));
    });

    // 创建并插入google字体使用的 link 标签
    const linkPreconnect1 = document.createElement('link');
    linkPreconnect1.rel = 'preconnect';
    linkPreconnect1.href = 'https://fonts.googleapis.com';

    const linkPreconnect2 = document.createElement('link');
    linkPreconnect2.rel = 'preconnect';
    linkPreconnect2.href = 'https://fonts.gstatic.com';
    linkPreconnect2.crossOrigin = 'anonymous';

    const linkStylesheet = document.createElement('link');
    linkStylesheet.href = 'https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap';
    linkStylesheet.rel = 'stylesheet';

    // 将这些链接添加到 head 中
    document.head.appendChild(linkPreconnect1);
    document.head.appendChild(linkPreconnect2);
    document.head.appendChild(linkStylesheet);

    // 清理函数，移除插入的标签
    return () => {
      document.head.removeChild(linkPreconnect1);
      document.head.removeChild(linkPreconnect2);
      document.head.removeChild(linkStylesheet);
    };
  }, []);

  return (
    <div className=' merriweather-light'>

      <NewNavbar />
      <Swiper
        spaceBetween={0}
        effect={'fade'}
        centeredSlides={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        // navigation={true}
        modules={[EffectFade, Autoplay, Pagination]}
        className="mySwiper"
        style={{ height: '100vh' }} // 设置轮播图高度
      >
        {slides.map((slide, index) => (
          <SwiperSlide key={index}>
            <div
              style={{
                height: '100%',
                background: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('${slide.src}') no-repeat center center/cover`,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                color: 'white', // 文字颜色
              }}
            >
              <div className='HomeTitleText merriweather-light'>
                <h1>{HomeH1}</h1>
                <p>One Barbershops</p>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>

      <div class="box">
        <div class="imagebox">
          <img src={side_background} alt="This is img"></img>
        </div>
        <div class="textbox merriweather-light" >
          <h1>Where Tradition <br></br>Meets Precision</h1>
          <p>Welcome to Campus Barbers, the iconic barbershop nestled right in the heart of East Lansing, Michigan, on the vibrant campus of Michigan State University. For over seven decades, quality service has been our unwavering commitment. Our barbers are not just professionals; they are experienced veterans in the world of barbering. With decades of expertise, they possess a keen understanding of the art and science of haircuts, ensuring that every client who walks through our doors leaves with a hairstyle that boosts their confidence and suits their individual style.</p>
        </div>
      </div>

      <div class="box reverse">
        <div class="imagebox">
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2930.6054423363025!2d-84.47846268749686!3d42.73324117104036!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8822e920ca8cbe01%3A0x7be6b1912c338548!2sAsian%20Beauty%20Salon%2C%20Inc.!5e0!3m2!1sen!2sus!4v1729915077890!5m2!1sen!2sus"
              width="100%"
              height="450"
              allowFullScreen=""
              loading="lazy"
              title="Barbershop Location"
            ></iframe>
        </div>
        <div class="textbox merriweather-light" >
          <h1>Asian Beauty Salon<br></br></h1>
          <h2>611 E Grand River Ave #200, East Lansing, MI 48823</h2>
          <p>Book your appointment or visit us at our location!</p>
          <div class="buttonbox">
            <button>navigation</button>
            <button>appointment</button>
          </div>
          
        </div>
      </div>
      <Routes>
        <Route path='/contact' element={<Contact />} />
        <Route path='/employees' element={<Employees />} />
        <Route path='/service' element={<Service />} />
      </Routes>

    </div>

  );
}

export default Home;